.c-btn {
  text-align: center;
  padding: 0.9375rem 0.625rem;
  font-size: 1rem;
  border-radius: 0.125rem;
  background: #fff;
}
.c-btn-group .c-btn {
  border-top: 0.0625rem solid #ddd;
}
.c-btn-group .c-btn:last-child {
  border-bottom: 0.0625rem solid #ddd;
}
.c-btn-border {
  border: 0.0625rem solid #ddd;
}
.c-btn-success {
  color: #58a347;
}
.c-btn-info {
  color: #7592cb;
}
.c-btn-error {
  color: #ea7b75;
}
.c-btn-warning {
  color: #e8a163;
}
