.c-spin {
  z-index: 100;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.c-spin-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.875rem;
  color: #fff;
  border-radius: 0.25rem;
  background-color: rgba(0, 0, 0, 0.5);
}
.c-spin-text {
  margin-top: 0.625rem;
}
